// - 320px - 767px   ;
// - 768px - 1023px   ;
// - 1024px     .


.container {
  // desktop
  width: 100%;
  min-width: 320px;
  //     1220
  //  40+40
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;

  // tablet
  @include vp-1023 {
    padding: 0 50px;
  }

  // mobile
  @include vp-767 {
    padding: 0 25px;
  }
}
